import React, { useState } from "react";
import "../App.css";

export default function TextForm(props) {
  const upFunc = () => {
    console.log("Up Was CLicked");
    // setText("You Have Clicked On The Button");
    setText(text.toUpperCase());
    // setText("Hi this is me ")
  };
  const lowFunc = () => {
    console.log("Lower");
    setText(text.toLowerCase());
  };
  const changefn = (event) => {
    console.log("on Click");
    setText(event.target.value);
  };

  const getLan = () => {
    var i = text.length;
    console.log(i);
    document.getElementById("length").value = i;
  };
  const setBold = () => {
    setText(text.bold());
  };

  const clear = () => {
    setText(" ");
  };

  const anchorTag = () => {
    setText(text.anchor());
  };

  const smallTag = ()=>{
    setText(text.small());
  }

  const bigTag = ()=>{
    setText(text.big());
  }

  const blinkTag= ()=>{
    setText(text.blink());
  }

  const [text, setText] = useState("Enter Text Here");
  return (
    <>
      <div className="mb-3">
        <h1>Enter Your Text To Customize</h1>
      </div>
      <div className="mb-3">
        <label for="exampleFormControlTextarea1" className="form-label">
          By Mad_Mods
        </label>
        <textarea
          className="form-control"
          id="exampleFormControlTextarea1"
          value={text}
          onChange={changefn}
          rows="8"
        ></textarea>
        <input
          type="submit"
          value="Upper-Case"
          onClick={upFunc}
          className="btn btn-primary mt-2"
        />
        <input
          type="submit"
          value="Lower-Case"
          onClick={lowFunc}
          className="btn btn-primary mt-2 mx-2"
        />
        <input
          type="submit"
          value="Length"
          onClick={getLan}
          id="length"
          className="btn btn-primary mt-2 mx-2 "
        />
        <input
          type="submit"
          value="Bold"
          onClick={setBold}
          className="btn btn-primary mt-2 mx-2"
        />
        <input
          type="submit"
          value="String"
          onClick={anchorTag}
          className="btn btn-primary mt-2 mx-2"
        />
        <input
          type="submit"
          value="Small"
          onClick={smallTag}
          className="btn btn-primary mt-2"
        />
        <input
          type="submit"
          value="Big"
          onClick={bigTag}
          className="btn btn-primary mt-2 mx-2"
        />
        <input
          type="submit"
          value="Blink"
          onClick={blinkTag}
          id="length"
          className="btn btn-primary mt-2 mx-2 "
        />
        <input
          type="submit"
          value="Clear"
          onClick={clear}
          className="btn btn-danger mt-2 mx-2"
        />
      </div>
    </>
  );
}
